import { Fragment, useEffect } from "react"
import { useTranslation } from "react-i18next"
import { useDispatch, useSelector } from "react-redux"
import DropDown from "./DropDown"
import RoleCards from "./RoleCards"
import { getApiSummaryData, getSummaryData, storeName } from "./store"
import "./style.scss"

const List = ({
  lookupMode = false,
  lookupMode_fieldName = "",
  lookupMode_dropdown = false,
  lookupMode_defaultValue,
  lookupMode_onChange,
  lookupMode_isMulti = false,
  errorState = false
}) => {
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])

  useEffect(() => {
    if (
      getApiSummaryData &&
      store.dataSummary?.length === 0 &&
      !store.isLoadingSummary
    ) {
      dispatch(getSummaryData({}))
    }
  }, [dispatch, store.dataSummary?.length > 0])

  const colors = [
    "primary",
    "success",
    "warning",
    "danger",
    "info",
    "secondary"
  ]

  return !lookupMode_dropdown ? (
    <Fragment>
      <h3>{t("Roles List")}</h3>
      <p className="mb-2">
        {t(
          "A role provides access to predefined menus and features depending on the assigned role to an administrator that can have access to what he needs."
        )}
      </p>
      <RoleCards />
      {/* <h3 className="mt-50">{t("Total users with their roles")}</h3>
      <p className="mb-2">
        {t(
          "Find all of your companyâ€™s administrator accounts and their associate roles."
        )}
      </p>
      <div className="app-user-list">
        <Table />
      </div> */}
    </Fragment>
  ) : (
    // <div className="app-user-list">
    //   {getApiSummaryData && (
    //     <Row>
    //       {store.dataSummary?.map((item, index) => {
    //         return (
    //           <Col>
    //             <StatsHorizontal
    //               color={colors[index]}
    //               statTitle={`${t(item.title)}`}
    //               icon={
    //                 item.icon ? (
    //                   <Icon
    //                     baseClassName="material-icons-outlined"
    //                     fontSize="20"
    //                   >
    //                     {item.icon}
    //                   </Icon>
    //                 ) : (
    //                   <Icon
    //                     baseClassName="material-icons-outlined"
    //                     fontSize="20"
    //                   >
    //                     lightbulb
    //                   </Icon>
    //                 )
    //               }
    //               renderStats={
    //                 store.isLoadingSummary ? (
    //                   <Skeleton
    //                     sx={{ bgcolor: "#eef2f8" }}
    //                     animation="wave"
    //                     height={25}
    //                     width={40}
    //                     className="fw-bolder mb-75"
    //                   />
    //                 ) : (
    //                   <h3 className="fw-bolder mb-75">
    //                     {item.count_format ?? item.count}
    //                   </h3>
    //                 )
    //               }
    //             />
    //           </Col>
    //         )
    //       })}
    //     </Row>
    //   )}
    //   <Table />
    // </div>
    <DropDown
      lookupMode_fieldName={lookupMode_fieldName}
      lookupMode_onChange={lookupMode_onChange}
      lookupMode_defaultValue={lookupMode_defaultValue}
      lookupMode_isMulti={lookupMode_isMulti}
      errorState={errorState}
    />
  )
}

export default List
